/*************************/
/*  PROGRESBAR */
/************************/

#progressbar {
	margin-bottom: 30px;
	overflow: hidden;
	/*CSS counters to number the steps*/
  counter-reset: step;
  margin-left: 18em;
  margin-right: 18em;

}
#progressbar li {
	list-style-type: none;
	color: black;
	text-transform: uppercase;
	font-size: 15px;
  width: 33.33%;
	float: left;
  position: relative;
  
}

#progressbar li:before {
	content: counter(step);
	counter-increment: step;
  width: 40px;
  height: 40px;
	line-height: 20px;
	display: block;
	font-size: 10px;
	color: grey;
	background: grey;
	border-radius: 3px;
	margin: 0 auto 5px auto;
}


.centar-inicio-administrador{
 display: block;
 margin:auto;
}

/*progressbar connectors*/
#progressbar li:after {
	content: '';
	width: 100%;
	height: 2px;
	background: grey;
	position: absolute;
	left: -50%;
	top: 9px;
	z-index: -1; /*put it behind the numbers*/
}

#progressbar li:first-child:after {
	/*connector not needed before the first step*/
	content: none; 
}

/*marking active/completed steps green*/
/*The number of the step and the connector before it = green*/
#progressbar li.active:before,  #progressbar li.active:after{
	background:rgb(14, 23, 105);
  color: white;
  
}

.fs-title {
	font-size: 25px;
	text-transform: uppercase;
	color: #2C3E50;
  margin-bottom: 10px;
  text-align: center;
}
.fs-subtitle {
	font-weight: normal;
	font-size: 20px;
	color: #666;
